:root {
    --slide-ease: cubic-bezier(0.4, -0.3, 0.6, 1.3);
    /*   --slide-ease: cubic-bezier(.8,-0.3,.2,1.3); */
    --easing: var(--slide-ease);
    --speed: 0.5s;
    --width: 80px;
    --ar: 8 / 3;
    --ray: hsla(0, 0%, 100%, 0.5);
    --sun: hsl(47, 91%, 58%);
    --moon: hsl(212, 13%, 82%);
    --crater: hsl(221, 16%, 68%);
    --bg: hsl(219, 30%, 88%);
    --bear-speed: 10s;
    --color: hsl(219, 30%, 20%);
}

[data-dark-mode="true"] {
    --bg: hsl(219, 30%, 12%);
    --color: hsl(219, 30%, 98%);
}

.wrapper{
    position: absolute;
    top: 20px;
    right: 20px;
}

.toggle__backdrop:first-of-type .clouds path:first-of-type {
    fill: var(--ray);
}

.toggle {
    -webkit-tap-highlight-color: transparent;
    width: var(--width);
    /* random attempts at tackling the overflow iOS issue */
    z-index: 10;
    will-change: transform;
    isolation: isolate;
    transform: translate3d(0, 0, 0);
    /* End of workaround city   */
    aspect-ratio: var(--ar);
    border-radius: 100vh;
    border: 0;
    position: relative;
    padding: 0;
    overflow: hidden;
    cursor: pointer;
    transition: background var(--speed) var(--easing);
    --sky: hsl(204, 53%, 47%);
    --night: hsl(229, 25%, 16%);
    outline-color: transparent;
    background: hsl(
            calc(204 + (var(--dark, 0) * 25)) calc((53 - (var(--dark, 0) * 28)) * 1%)
            calc((47 - (var(--dark, 0) * 31)) * 1%)
    );
    box-shadow: calc(var(--width) * 0) calc(var(--width) * 0.02)
    calc(var(--width) * 0.01) calc(var(--width) * -0.0025)
    hsla(210, 10%, 100%, 0.95),
    calc(var(--width) * 0) calc(var(--width) * -0.02) calc(var(--width) * 0.01)
    calc(var(--width) * -0.0025) hsla(210, 10%, 10%, 0.2),
    calc(var(--width) * 0) calc(var(--width) * 0.02) calc(var(--width) * 0.5) 0
    hsla(210, 10%, 100%, 0.15);
}

.toggle:after {
    content: "";
    position: absolute;
    inset: 0;
    box-shadow: calc(var(--width) * 0) calc(var(--width) * -0.025)
    calc(var(--width) * 0.025) 0 hsla(210, 10%, 10%, 0.15) inset,
    calc(var(--width) * 0) calc(var(--width) * 0.025) calc(var(--width) * 0.025)
    0 hsla(210, 10%, 10%, 0.65) inset;
    border-radius: 100vh;
}

.toggle__content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    border-radius: 100vh;
    display: block;
    clip-path: inset(0 0 0 0 round 100vh);
}

.toggle__backdrop {
    overflow: visible !important;
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    transition: translate var(--speed) var(--easing);
    translate: 0 calc(var(--dark, 0) * (100% - (3 / 8 * var(--width))));
}
